<template>
    <div>
        <form @submit="addTodo">
            <input type="text" v-model="title" name="title"
            placeholder="Add new task">
            <input type="submit" value="Submit"
            class="btn">
        </form>
    </div>
</template>

<script>
export default {
    name: "addTodo",
    data(){
        return{
            title: ''
        }
    },
    methods:{
        addTodo(e){
            e.preventDefault();
            const newTodo ={
                // after Installed UUID
                id: this.uuidv4(),
                title: this.title, // from constructor
                completed: false
            }
            // Send up to parent
            this.$emit('add-todo',newTodo);

            this.title = '';
        },
        uuidv4() {
            return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
            return v.toString(16);
        });
}
    }
}
</script>

<style  scoped>
form {
    display: flex;
}

input[type="text"] {
    flex: 10;
    padding: 5px;
}

input[type="submit"] {
    flex: 2;
}
</style>